.github-card {
    background-color: rgb(255, 255, 255);
    font-size: medium;
    margin: 10px;
    border-radius: 5px;

    -webkit-box-shadow: 10px 10px 75px -17px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 10px 75px -17px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 10px 75px -17px rgba(0, 0, 0, 0.75);
}

.github-card .details {
    color: rgb(56, 56, 56);
    text-decoration: none;
}

.github-card .details .name {
    display: flex;
    border-bottom-style: inset;
    border-bottom-width: 2px;
}


.github-card .details .name a {
    margin-top: 2px;
    margin-left: 4px;
    color: rgb(56, 56, 56);
}

.github-card .details .name a i {
    font-size: medium;
}


.github-card .details .description-lang .desc {
    font-family: 'Roboto', sans-serif;
    text-align: left;
    text-justify: inter-word;
}

.github-card .details .description-lang .lang {
    font-family: 'Roboto', sans-serif;
    list-style: none;
}

.github-card .details .description-lang .lang .lang-row {
    display: flex;
    min-width: max-content;
    margin-right: 10px;
}

.github-card .details .description-lang .lang .lang-color {
    margin-top: 5px;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    margin-right: 4px;
}

.github-card .details .active-date {
    text-align: left;
}