@import url(https://fonts.googleapis.com/css2?family=Quicksand&family=Roboto&family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap);
html {
  scroll-behavior: smooth;
}

html {
  font-size: 1rem;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* ScrollBar Styles */

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background: #6c757d; 
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}
::-webkit-scrollbar-thumb:window-inactive {
background: rgba(25, 25, 25, 0.4); 
}
.app {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}


.app-body {
  margin-left: 250px;
  background-color: #e4e4e4;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #484d53;
}

.model-container {
  display: flex;
  flex-direction: column;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
.home-body p {
    font-family: 'Fira Sans', sans-serif;
    font-size: 3em;
}

.home-body p span{
    font-size: 1em;
}

.home-body span {
    font-family: 'Fira Sans', sans-serif;
    font-size: 1em;
}

.home-body span .odessa {
    text-decoration: none;
    color: #57cdff;
}

.home-body span .mit {
    text-decoration: none;
    color: orange;
}

.github-card {
    background-color: rgb(255, 255, 255);
    font-size: medium;
    margin: 10px;
    border-radius: 5px;
    box-shadow: 10px 10px 75px -17px rgba(0, 0, 0, 0.75);
}

.github-card .details {
    color: rgb(56, 56, 56);
    text-decoration: none;
}

.github-card .details .name {
    display: flex;
    border-bottom-style: inset;
    border-bottom-width: 2px;
}


.github-card .details .name a {
    margin-top: 2px;
    margin-left: 4px;
    color: rgb(56, 56, 56);
}

.github-card .details .name a i {
    font-size: medium;
}


.github-card .details .description-lang .desc {
    font-family: 'Roboto', sans-serif;
    text-align: left;
    text-justify: inter-word;
}

.github-card .details .description-lang .lang {
    font-family: 'Roboto', sans-serif;
    list-style: none;
}

.github-card .details .description-lang .lang .lang-row {
    display: flex;
    min-width: -webkit-max-content;
    min-width: -moz-max-content;
    min-width: max-content;
    margin-right: 10px;
}

.github-card .details .description-lang .lang .lang-color {
    margin-top: 5px;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    margin-right: 4px;
}

.github-card .details .active-date {
    text-align: left;
}
.work-experience h4 {
    justify-content: start;
    text-align: left;
}

.work-experience p {
    text-align: left;
    text-justify: inter-word;
}
