.grid{
    display: inline-flex;
    border-radius: 10px;
}

.grid-cell{
    background-color: rgb(241, 241, 241);
    width: 1.5em;
    height: 1.5em;
    /* border-radius: 1em; */
    margin: 0.1em;
    box-shadow: 1px 2px 5px 0px rgba(0,0,0,0.75);
    
}

.grid-cell-wall{
    background-color: rgb(41, 41, 41);
}

@media screen and (min-width: 421px) {
.grid-cell:hover{
    background-color: rgb(14, 191, 231);
    box-shadow: 2px 2px 5px 0px rgba(113, 113, 113, 0.75);
}
}

@media screen and (max-width: 420px) {
    .grid-cell:focus{
        background-color: rgb(14, 191, 231);
        box-shadow: 2px 2px 5px 0px rgba(113, 113, 113, 0.75);
    }
}

.grid-cell-start{
    background-color: rgb(202, 81, 0);    
    border-radius: 1em;
}

.grid-cell-end{
    background-color: rgb(0, 187, 97);    
    border-radius: 1em;
}

.grid-cell-path{
    background-color: rgb(0, 118, 221); 
    border-radius: 0.5em;
}

.grid-cell-open{
    background-color: rgb(215, 200, 0);
    /* border-radius: 1em; */
}

.grid-cell-closed{
    background-color: rgb(215, 200, 0);
}

.table td{
    width: 30px;
    height: 30px;
}
